/*--------------BURGER MENU------------*/
.burger-menu {
  position: absolute;
  width: 20px;
  height: 20px;
  cursor: pointer;
  right: 15px;
  top: 27px;
  display: none;
}

.burger {
  position: absolute;
  background: #fff;
  width: 100%;
  height: 3px;
  top: 50%;
  right: 0;
  opacity: 1;
}

.burger::before {
  position: absolute;
  background: #fff;
  width: 20px;
  height: 3px;
  top: 7px;
  content: "";
  display: block;
}

.burger::after {
  position: absolute;
  background: #fff;
  width: 20px;
  height: 3px;
  bottom: 7px;
  content: "";
  display: block;
}

.burger::after, .burger::before, .burger {
  transition: all .3s ease-in-out;
  -webkit-transition: all .3s ease-in-out;
}

.burger-menu.menu-on .burger::after {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  bottom: 0px;
}

.burger-menu.menu-on .burger::before {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  top: 0px;
}

.burger-menu.menu-on .burger {
  background: rgba(111, 111, 111, 0);
}

