  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader__inner {
  position: relative;
  width: 12px;
  height: 12px;
  border-radius: 12px;
  background-color: #fff;
  transform-origin: 50% 50%;
  animation: loader 1s ease-in-out infinite;
}

