  color: #d1d1d1;
  font-size: 16px;
}

.fc-event.cal-offer .move:hover,
.fc-event.cal-offer .delete:hover {
  color: #ffffff;
}

.fc-event.cal-offer .delete {
  position: absolute;
  right: 8px;
  top: 12px;
  z-index: 5;
}

.sidebar__block {
  margin-top: 15px;
}

.btn-block {
  font-size: 14px;
  font-weight: 700;
  background: #003856;
  padding: 12px 0;
  border-radius: 0;
  display: block;
  color: #fff;
}

.btn-block:hover,
.btn-block:focus {
  color: #fff;
  background: #072334;
}

.generate-link__generated-link {
  border: 1px solid #ccc;
  margin-top: 10px;
  width: 100%;
  padding: 7px 9px;
  display: none;
}
